<template>
  <v-container>
      <v-layout row wrap>
          <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
            <img src="static/images/logos/easyvedaznamelogo-192x192.png" width="120px"/>
            <br/><br/>
            <h2>||Bhagvad Gita||</h2>
            <h3>Select Language</h3><br/>
          </v-flex>

          <v-flex xs12 sm6 offset-sm3 class="text-xs-center" v-for="lang in languages" :key="lang.code">
              <v-btn block large round color="error" @click="selectLangandGetVerses(lang.code)">{{lang.name}}</v-btn>
          </v-flex>
      </v-layout>
  </v-container>
</template>
<script>
import {HTTP} from '@/common/http-common'
export default {
  data () {
    return {
      languages: [],
      errors: []
    }
  },

  mounted () {
    this.$store.dispatch('isLoading', true)
    HTTP.get('languages').then(response => {
      this.languages = response.data
      this.$store.dispatch('isLoading', false)
    }).catch(e => {
      this.errors.push(e)
    })
  },

  methods: {
    selectLangandGetVerses (langCode) {
      this.$store.dispatch('fetchVerses', {lang: langCode})
      this.$router.push({
        path: 'Verse'
      })
    }
  }
}
</script>



